<template lang="pug">
.md-layout
	.md-layout-item
		img(src="../assets/biohazard.svg" width="175px")
		h1 {{ msg }}

		h2 Hawking Talking
		H3 Com Webpack e Clonado na Samboja
		md-button.md-raised.md-primary(to="/")
			md-icon waves
			| &nbsp; Into the Future
		h4
			i.fa.fa-street-view.fa-5x
</template>

<script>
export default {
	name: 'HelloWorld',
	data () {
		return {
		msg: 'Breve História do Tempo'
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
h1, h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}
</style>



